@mixin flex-end-positioning {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  margin-bottom: 20px;
}

@mixin center-sb-positioning {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin card-image-size {
  width: $card-image-width;
  height: $card-image-height;
}
